import React,{useState,useEffect} from 'react';
import axios from 'axios'
import {useNavigate,useLocation} from 'react-router-dom'
import {useSelector} from 'react-redux'
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CameraAltSharpIcon from '@material-ui/icons/CameraAltSharp';
import Input from '@material-ui/core/Input';
import Navbar from './Navbar';

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  
}));

function EditArticle() {
  const location=useLocation()
  const classes = useStyles();
  
    const [article,setArticle]=useState({})
    const [inputs,setInputs]=useState([])
    const [file,setFile]=useState({selectedFile:''})
    const navigate=useNavigate()
    const token=useSelector((state)=>state.token.value)

    useEffect(() => {
      fetchData() 
      console.log(location.state.name)  

    }, []);
    
    const fetchData=async()=>{
        const access_token='eyJhbGciOiJIUzI1NiJ9.aW5kdW1zQGdtYWlsLmNvbQ.mPbFwa2dfpuBaYG7M8R85P-7dqVe9yykJqtgusV7Des'//token
    console.log(access_token)
try {
  const result=await axios(
    {
      url:`/api/article/${location.state.name}`,
      method:'GET',
      headers:{
        'Authorization':`Basic ${access_token}`
      }
    })

setArticle(result.data)
setInputs(result.data)

} catch (error) {
  console.log(error)
}
  }

    
    const handleChange=(e)=>{
        const {name,value}=e.target
        setInputs(values=>({...values,[name]:value}))
        console.log(inputs)
    }
    
    const fileChange=(e)=>{
        setFile({selectedFile:e.target.files[0]})
        console.log(file)
    }
    
    const handleSubmit=async(e)=>{
       //e.preventDefualt()
        const access_token=token
        const formData=new FormData
        formData.append('name',inputs.name)
        formData.append('content',inputs.content)
        formData.append('id',article._id)
        if(file.selectedFile)
       formData.append('image',file.selectedFile,file.selectedFile.name);
        try {        
            const result=axios(
                {
                    url:'http://localhost:8080/article/update/',
                    data:formData,
                    method:'PUT',
                                    headers:{
                        'Authorization':`Basic ${access_token}`,
                        'Content-Type':'multipart/form-data'
                      }
                }          
        )  
        console.log(result)
        navigate('/articlePageAdmin')
        } catch (error) {
            console.log('hi')
        }
    } 
      return (
        <div>
          <Navbar/>,
        <Container component="main" maxWidth="xs">
  <CssBaseline />
  <div className={classes.paper}>
    <Avatar className={classes.avatar}>
      <CameraAltSharpIcon />
    </Avatar>
    <Typography component="h1" variant="h5">
      Add
    </Typography>
    <form className={classes.form} onSubmit={handleSubmit} method='POST'>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={12}>
          <TextField
            autoComplete="fname"
            name="name"
            variant="outlined"
            fullWidth
            id="firstName"
            autoFocus
            value={inputs.name}
            onChange={handleChange}
          />
         
        </Grid>
        
        <Grid item xs={12}>-
          <TextField
            variant="outlined"
            fullWidth
            multiline
            id="email"
            name="content"
            autoComplete="email"
            value={inputs.content}
            onChange={handleChange}
          />
         
        </Grid>
        
        <Grid item xs={12}>
      
          <Input
            type="file"
            fullWidth
            name="file"   
            onChange={fileChange}           
            />
         
        </Grid>
       
      </Grid>
      <Button
        type="submit"
        onClick={handleSubmit}
        fullWidth
        variant="contained"
        color="primary"
        className={classes.submit}
      >
        Add Article
      </Button>
      <Grid container justifyContent="flex-end">
        <Grid item>
         
        </Grid>
      </Grid>
    </form>
  </div>
  
</Container>
    </div>
      )
}

export default EditArticle;
