import React,{useState,useEffect} from 'react';
import axios from 'axios';
import {useSelector} from 'react-redux'
import {useNavigate,Link} from 'react-router-dom'
import Navbar from './Navbar';

import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import {Grid} from '@material-ui/core'
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import {Button} from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete';
import Icon from '@material-ui/core/Icon';
import {ThemeProvider,createTheme} from '@material-ui/core/styles';
import { green} from '@material-ui/core/colors';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 500,
    display:"inline-block",
    marginLeft:"200px",
    marginTop:"100px",
    
    
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
  button: {
    margin: theme.spacing(1),
  },
gridd:{
  marginTop:"30px"
}

}));

const theme = createTheme({
  palette: {
    primary: green,
  },
});


function ArticlePageAdmin() {
    const [article,setArticle]=useState([])
    const token=useSelector((state)=>state.token.value)
    const navigate=useNavigate()
    const classes = useStyles();

 async function del(id){
  try {
    const access_token=token
    console.log(id)
    
    const result=await axios(
      {
        url:`/api/article/delete/${id}`,
        method:'DELETE',
        headers:{
          'Authorization':`Basic ${access_token}`
        }
      }) 
  
  fetchArticles()
  
  } catch (error) {
    console.log(error)
  }
}

  useEffect(() => {
   fetchArticles()
  }, []);

  const addArticle=()=>{
    navigate('/addArticle')
  }
  
  const edit=(id)=>{
    navigate('/editArticle',{state:{name:id}})
  }

  const fetchArticles=async()=>{
    const access_token=token
    console.log(access_token)
try {
  const result=await axios(
    {
      url:'/api/article/',
      method:'GET',
      headers:{
        'Authorization':`Basic ${access_token}`
      }
    })

setArticle(result.data)
  console.log(result.data)


} catch (error) {
  console.log(error)
}

  }  
  return (
    <div>
      <Navbar/>,
   <Grid className='root'>
   <ThemeProvider theme={theme}>
      <Button onClick={addArticle} variant="contained" color="primary" className={classes.margin}>
        Add New Article
      </Button>
      </ThemeProvider>
   </Grid>,    
   
    {
      article.map((item,key)=>(
        <Card className={classes.root}>
<CardHeader
  avatar={
    <Avatar aria-label="recipe" className={classes.avatar}>
      Mine
    </Avatar>
  }
  action={
    <IconButton aria-label="settings">
      <MoreVertIcon />
    </IconButton>
  }
  title={item.name}
  subheader={item.createdOn}
/>
<CardMedia
  className={classes.media}
  image={item.imgUrl}
  title="Paella dish"
/>
<CardContent>
  <Typography variant="body2" color="textSecondary" component="p">
    {item.content}
  </Typography>
</CardContent>
<CardActions disableSpacing>
  <IconButton aria-label="add to favorites">
    <FavoriteIcon />
  </IconButton>
  <IconButton aria-label="share">
    <ShareIcon />
  </IconButton>
 
<Button
  onClick={()=>edit(item.id)}
  variant="contained"
  color="primary"
  className={classes.button}
  endIcon={<Icon>send</Icon>}
>
  Edit
</Button>
<Button
  onClick={()=>del(item.id)}
  variant="contained"
  color="secondary"
  className={classes.button}
  startIcon={<DeleteIcon />}
>
  Delete
</Button>
</CardActions>

</Card> 
     
    
        ))}
        </div>
  
  );
}

export default ArticlePageAdmin;

