import React,{useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { FormLabel } from '@material-ui/core';
import axios from 'axios'
import {useSelector,useDispatch} from 'react-redux'
import {change_user} from '../redux/user/userSlice'
import {change_token} from '../redux/user/tokenSlice'
import {useNavigate} from 'react-router-dom'
import Header from './Header';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {`Copyright © ${new Date().getFullYear()}.`}</Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  error:{
    color:'red'
  }
}));


function Login() {
  //use redux to change state
  const user=useSelector(userState=>userState.user.value)
  const token=useSelector(tokenState=>tokenState.token.value)
  const dispatch=useDispatch()

  const navigate=useNavigate()

  const classes = useStyles();
  const [inputs,setInputs]=useState({})
  const [err,setErr]=useState({})

  const handleSubmit=async(e)=>{
   e.preventDefault()
   let valid=validate(inputs)
   if(valid)
   {
    var userLogin={email:inputs.email,password:inputs.password}

    try {
      const result=await axios(
        {
          url:'/api/login',
          method:'POST',
          data:userLogin
        }
      )
      console.log(result.data)
      dispatch(change_user({user:result.data.user}))
      dispatch(change_token({token:result.data.token}))
      if(result.data.user==='admin')          
      navigate('/articlePageAdmin')
      else
      navigate('/articlePageUser')

    } catch (error) {
      
    }
          
   }   
   
  }

const  handleChange=(e)=>{
const {name,value}=e.target
setInputs(values=>({...values,[name]:value}))
}

const validate=(input)=>{ 
   let errors = {};
  let formIsValid = true;

 
  if (!input["email"]) {
    formIsValid = false;
    errors["email"] = "*Please enter your email-ID";
  }

  
    //regular expression for email validation
    let regexpemail=/^(\w+([\.-]?\w+))@([a-zA-Z0-9\-]+)\.([a-z]{2,3})(.[a-z]{2,3})?$/;             
    if(regexpemail.test(input["email"])===false)  
    {
      formIsValid = false;
      errors["email"] = "*Please enter valid email-ID";
    }
  

  if (!input["password"]) {
    formIsValid = false;
    errors["password"] = "*Please enter your password.";
  }

  let regexppwd=/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
  if(regexppwd.test(input["password"])===false)
  { 
    formIsValid = false;
      errors["password"] = "*Please enter secure and strong password.";           
 }
  setErr(errors);
  return formIsValid;


}


  return (
    <div>
    <div>
      <Header/>
      </div>
      <div>
    <Container component="main" maxWidth="xs">      
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign In
        </Typography>

        <form className={classes.form} onSubmit={handleSubmit} method='POST'>
          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={handleChange}
            value={inputs.email}
          />
          <FormLabel className={classes.error}>{err.email}</FormLabel>

          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={handleChange}
            value={inputs.password}
          />
           <FormLabel className={classes.error}>{err.password}</FormLabel><br/>

          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />

          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="#" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>

      </div>
      <Box mt={8}>
        <Copyright />
      </Box>
    </Container>
    </div>
    </div>
  );
}

export default Login;
