import React,{useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import {FormLabel} from '@material-ui/core'
import axios from 'axios'
import Header from './Header';
import { Navigate } from 'react-router-dom';
import {useNavigate} from 'react-router-dom' 

function Copyright() {
    return (
      <Typography variant="body2" color="textSecondary" align="center">
        {`Copyright © ${new Date().getFullYear()}.`}</Typography>
    );
  }

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  error:{
    color:'red'
  },
}));


function Signup() {
    const classes = useStyles();
    const [inputs,setInputs]=useState({})
  const [err,setErr]=useState({})
  const navigate=useNavigate()


  const handleSubmit=async(e)=>{
   e.preventDefault()
   let valid=validate(inputs)
   if(valid)
   {
    
    var user={fname:inputs.fname,lname:inputs.lname,password:inputs.password,email:inputs.email}
    console.log(user)
    try {
        await axios(
            {
              url:'/api/signup/',
              method:'POST',
              data:user
            }
          )
navigate('/login')
          
    } catch (error) {  
        alert('Email-ID already taken.try a new one')
       }    
     
   }
  }

const  handleChange=(e)=>{
const {name,value}=e.target
setInputs(values=>({...values,[name]:value}))
}

const validate=(input)=>{
   let errors = {};
  let formIsValid = true;

  if (!input["fname"]) {
    formIsValid  = false;
    errors["fname"] = "*field cant be empty";
  }

  if (!input["lname"]) {
    formIsValid  = false;
    errors["lname"] = "*field cant be empty";
  }

  let regfname=/^[a-zA-Z ]*$/
  if(regfname.test(input["fname"])===false)  
   {
    formIsValid  = false;
     errors["fname"] = "*alphabets only";
   }

   if(regfname.test(input["lname"])===false)  
   {
    formIsValid  = false;
     errors["lname"] = "*alphabets only";
   }

    
  if (!input["email"]) {
    formIsValid = false;
    errors["email"] = "*Please enter your email-ID";
  }

  
    //regular expression for email validation
    let regexpemail=/^(\w+([\.-]?\w+))@([a-zA-Z0-9\-]+)\.([a-z]{2,3})(.[a-z]{2,3})?$/;             
    if(regexpemail.test(input["email"])===false)  
    {
      formIsValid = false;
      errors["email"] = "*Please enter valid email-ID";
    }
  

  if (!input["password"]) {
    formIsValid = false;
    errors["password"] = "*enter a password.";
  }

  let regexppwd=/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/;
  if(regexppwd.test(input["password"])===false)
  { 
    formIsValid = false;
      errors["password"] = "*enter strong password as specified";           
 }
  setErr(errors);
  return formIsValid;

}



  return (
    <div>
      <Header/>
  
         <Container component="main" maxWidth="xs">
    <CssBaseline />
    <div className={classes.paper}>
      <Avatar className={classes.avatar}>
        <LockOutlinedIcon />
      </Avatar>
      <Typography component="h1" variant="h5">
        Sign up
      </Typography>
      <form className={classes.form} onSubmit={handleSubmit} method='POST'>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              autoComplete="fname"
              name="fname"
              variant="outlined"
              fullWidth
              id="firstName"
              label="First Name"
              autoFocus
              value={inputs.fname}
              onChange={handleChange}
            />
            <FormLabel className={classes.error}>{err.fname}</FormLabel>
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              variant="outlined"
              fullWidth
              id="lastName"
              label="Last Name"
              name="lname"
              autoComplete="lname"
              value={inputs.lname}
              onChange={handleChange}
            />
            <FormLabel className={classes.error}>{err.lname}</FormLabel>
          </Grid>
          <Grid item xs={12}>-
            <TextField
              variant="outlined"
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              value={inputs.email}
              onChange={handleChange}
            />
            <FormLabel className={classes.error}>{err.email}</FormLabel>
          </Grid>
          <Grid item xs={12}>
            <TextField
              variant="outlined"
              fullWidth
              name="password"
              label="Should contain atleast one small letter,capital letter,digit.minimum 8 charecters"
              type="password"
              id="password"
              autoComplete="current-password"
              value={inputs.password}
              onChange={handleChange}
              />
              <FormLabel className={classes.error}>{err.password}</FormLabel>
            
          </Grid>
          <Grid item xs={12}>
            <FormControlLabel
              control={<Checkbox value="allowExtraEmails" color="primary" />}
              label="I want to receive inspiration, marketing promotions and updates via email."
            />
          </Grid>
        </Grid>
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
        >
          Sign Up
        </Button>
        <Grid container justifyContent="flex-end">
          <Grid item>
            <Link href="#" variant="body2">
              Already have an account? Sign in
            </Link>
          </Grid>
        </Grid>
      </form>
    </div>
    <Box mt={5}>
      <Copyright />
    </Box>
  </Container>
  </div>
  );
}

export default Signup;
